            dztext:{text1:'',text2:''},
            p2btn:[
                {name:'',imgurl:'p2-btn1.png',href:'https://www.jx3box.com/bps/#/?subtype=%E5%AD%A4%E9%94%8B%E8%AF%80'},
                {name:'',imgurl:'p2-btn2.png',href:'https://www.jx3box.com/macro/#/?subtype=%E5%AD%A4%E9%94%8B%E8%AF%80'},
                {name:'',imgurl:'p2-btn3.png',href:'https://www.jx3box.com/bps/#/skill?subtype=%E5%AD%A4%E9%94%8B%E8%AF%80'},
